@import url(https://fonts.googleapis.com/css?family=Overlock:400,400i,700|Oleo+Script);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,100,300,500,700,900);
html{
    /*Hsl Values*/
    --black: #3c3c3c;
    --dark-green: #164a41;
    --medium-green: #4d774e;
    --light-green: #9dc88d;
    --yellow: #f1b24a;
    --light: #f1f1f1;

    /*Font Size*/
    --fs-xl: 2.5rem;
    --fs-lg: 2.0rem;
    --fs-md: 1.5rem;
    --fs-sm: 1rem;
    
    /*Font Weight*/
    --text-normal: 500;
    --text-bold: 900;
}
@media (min-width: 1000px){
    html{
        --fs-xl: 3.5rem;
        --fs-lg: 3.0rem;
        --fs-md: 1.5rem;
        --fs-sm: 1.125rem;
    }
}

/*Global Styling*/
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behaviour: smooth;
}
img{
    width: 100%;
    display: block;
}
body{
    background: #164a41;
    background: var(--dark-green);
    color: #f1f1f1;
    color: var(--light);
    font-family: 'Roboto', sans-serif;
}
h1, h2,h3,h4,h5{
    font-family: 'Overlock', Arial, Helvetica, sans-serif;
}
a{
    text-decoration: none;
}
ul{
    list-style: none;
}

.App {
