.hat {
  width: 80px;
  height: 50px;
  background: var(--medium-green);
  border-radius: 3px 2px 0 0;
  position: relative;
  z-index: 10;
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
  border: 2px solid rgba(0, 0, 0, 0.3);
}
.hat::after {
  content: "";
  width: 150%;
  height: 5px;
  background: var(--medium-green);
  position: absolute;
  bottom: -5px;
  left: -25%;
  border: 2px solid rgba(0, 0, 0, 0.3);
}
.toyhead {
  height: 80px;
  width: 90px;
  border-radius: 50%;
  background: #f6d0a6;
  position: relative;
  z-index: 7;
}
.toyhead::after {
  content: "";
  height: 10px;
  width: 38px;
  background: #f6d0a6;
  position: absolute;
  bottom: -5px;
  right: 25px;
}
.eyes {
  background: var(--light);
  height: 20px;
  width: 20px;
  border-radius: 50%;
  position: absolute;
  top: 25px;
}
.eyes::before {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  background: var(--black);
  border-radius: 50%;
  top: 2.5px;
  left: 2.5px;
}
.eyes::after {
  content: "";
  position: absolute;
  width: 3px;
  height: 3px;
  background: var(--light);
  border-radius: 50%;
  top: 4px;
  left: 4px;
}
.eye-right::after {
  left: 13px;
}
.eye-left {
  position: absolute;
  left: 20px;
}
.eye-right {
  position: absolute;
  right: 20px;
}
.mouth {
  position: absolute;
  left: 28%;
  bottom: 10%;
  background: var(--black);
  width: 40px;
  height: 20px;
  overflow: hidden;
  border-radius: 10px 10px 70px 70px;
}
.mouth::before {
  content: "";
