  );
  position: absolute;
  top: -16px;
  right: 10px;
  border-radius: 5px 5px 0 0;
  border: 1px solid var(--black);
  border-bottom: 0;
}
.coffecap::after {
  content: "";
  height: 8px;
  width: 15px;
  background: var(--green);
  position: absolute;
  top: -22px;
  right: 15px;
  border-radius: 9px 0 0 0;
}
.contact__details i {
  color: var(--green);
  font-weight: var(--text-bold);
}
@media (max-width: 1000px) {
  .view {
    background: linear-gradient(45deg, #000, #555, #000);
    height: 120px;
    width: 80px;
    margin-top: 3rem;
    margin-bottom: 1.2rem;
    border: 12px solid var(--medium-green);
    border-left: 3px solid var(--medium-green);
    border-right: 3px solid var(--medium-green);
    border-radius: 8px;
    position: relative;
  }
  .view::before {
    content: "";
    background: linear-gradient(var(--green), var(--light-green), var(--green));
    height: 12px;
    width: 15px;
    position: absolute;
    bottom: -12px;
    left: 1.8rem;
    border: 2px solid var(--black);
    border-radius: 5px;
  }
  .view::after {
    content: "nokia";
    font-weight: var(--text-normal);
    position: absolute;
    font-size: 10px;
    top: -13px;
    right: 0;
  }
  .view-text::after {
    content: "mobile";
    color: var(--white);
    font-weight: var(--text-bold);
    font-family: "Overlock", Arial, Helvetica, sans-serif;
    position: relative;
    top: 40px;
    right: 0;
  }
  .view-text::before {
    content: "";
    background: var(--light-green);
    width: 14px;
    height: 5px;
    position: absolute;
    top: -7px;
    left: 41%;
    border: 2px solid var(--black);
    border-radius: 5px;
  }
}
@media (min-width: 1000px) {
  .view {
    background: linear-gradient(145deg, #000, #555, #000);
    width: 150px;
    height: 100px;
    border: 5px solid var(--medium-green);
    border-radius: 8px;
    margin-top: 3rem;
    margin-bottom: 1.8rem;
    position: relative;
    z-index: 2;
  }
  .view::before {
    content: "";
    position: absolute;
    bottom: -5px;
    right: 5px;
    background: var(--light-green);
    width: 8px;
    height: 5px;
    border: 1px solid var(--black);
    border-radius: 5px;
  }
  .view::after {
    content: "dell";
    background: var(--medium-green);
    font-size: 10px;
    width: 70px;
    height: 2px;
    position: absolute;
    bottom: -31px;
    left: 35px;
    border-bottom: 14px solid var(--medium-green);
    border-radius: 8px 0 8px 0;
  }
  .view-text::after {
    content: "";
    border: 3px solid var(--green);
    width: 50px;
    height: 20px;
    border-bottom: 0;
    border-top: 0;
    position: absolute;
    bottom: -25px;
    right: 45px;
  }
  .view-text::before {
    content: "desktop";
    color: var(--white);
    font-weight: var(--text-bold);
    position: relative;
    font-family: "Overlock", Arial, Helvetica, sans-serif;
    top: 30px;
    right: 3px;
  }
}

